.app__specialMenu {
    background: var(--color-black);
    display: flex;
    flex-direction: column;
}
.app__specialMenu-title{
    text-align: center;
    margin-bottom: 2rem;
}
.app__specialMenu-menu {
    width: 100%;
    margin: 2rem 0;

    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;
}
.app__specialMenu-menu_wine,
.app__specialMenu-menu_cocktails {
    flex: 1;
    width: 100%;
    flex-direction: column;
}
.app__specialMenu-menu_heading {
    color: var(--color-white);
    font-family: var(--font-base);
    font-size: 45px;
    font-weight: 600;
    line-height: 58px;
    letter-spacing: 0.04em;
    cursor: default;
    transition: 0.05s;
}
.app__specialMenu-menu_heading:hover{
    color: var(--color-golden);
}
.app__specialMenu_menu_items {
    display: flex;
    flex-direction: column;

    margin: 2rem 0;
    width: 100%;
}
.app__specialMenu-menu_img {
    width: 410px;
    margin: 0 2rem;
}
.app__specialMenu-menu_img img {
    width: 100%;
    height: auto;
}
@media screen and (max-width: 2000px){
    .app__specialMenu-menu_img {
        width: 650px;
    }
    .app__specialMenu-menu_img img {
        height: auto;
    }
}
@media screen and (max-width: 1150px){
    .app__specialMenu-menu {
        flex-direction: column;
        align-items: center;
        width: 100%;
    }
    .app__specialMenu-menu_img {
        margin: 3rem 0;
    }
}
@media screen and (max-width: 650px){
    .app__specialMenu-menu_img {
        width: 100% ;
    }
    .app__specialMenu-menu_heading {
        font-size: 35px;
        line-height: 48.5px;
    }
}